<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-tabs v-model="activeName" @tab-click="handleClick">
      <!--  -->
      <el-tab-pane label="" name="all"></el-tab-pane>
      <!--  -->
      <el-tab-pane label="" name="supply"></el-tab-pane>
      <!--  -->
      <el-tab-pane label="" name="demand"></el-tab-pane>
    </el-tabs>
    <el-card class="article-card">
      <!--  -->
      <div>
        <el-button type="danger" size="small" icon="el-icon-delete" @click="handleBatchRemove()"></el-button>
        <el-button type="primary" size="small" icon="el-icon-circle-plus" @click="addRow"></el-button>
        <!--        <el-select-->
        <!--          v-model="listQuery.productCategory"-->
        <!--          size="small"-->
        <!--          placeholder=""-->
        <!--          clearable-->
        <!--          class="filter-item"-->
        <!--          style="width: 130px;margin-left: 2rem"-->
        <!--        >-->
        <!--          <el-option v-for="item in productOptions" :key="item" :label="item" :value="item" />-->
        <!--        </el-select>-->
        <el-input
          v-model="listQuery.title"
          size="small"
          style="width: 120px;margin-left: 20px"
          clearable
          placeholder=""
        ></el-input>
        <el-input
          v-model="listQuery.contactPerson"
          size="small"
          style="width: 120px;margin-left: 20px"
          clearable
          placeholder=""
        ></el-input>

        <el-select
          v-model="listQuery.status"
          size="small"
          placeholder=""
          clearable
          class="filter-item"
          style="width: 130px;margin-left: 0.5rem"
        >
          <el-option v-for="item in stateTypeOptions" :key="item.key" :label="item.display_name" :value="item.key" />
        </el-select>
        <el-button
          class="filter-item"
          size="small"
          type="primary"
          style=";margin-left: 0.5rem"
          icon="el-icon-search"
          @click="handleFilter"
        >
          
        </el-button>
      </div>
      <!--  -->
      <el-table
        v-loading="listLoading"
        :data="rows"
        border
        stripe
        fit
        highlight-current-row
        :row-style="{ height: '5px' }"
        :cell-style="{ padding: '5px 0' }"
        :height="curHeight"
        @selection-change="checkSelect"
      >
        <el-table-column type="selection" width="40" label=""></el-table-column>
        <el-table-column align="center" label="" width="60">
          <template slot-scope="scope">
            {{ (listQuery.page - 1) * listQuery.size + scope.$index + 1 }}
          </template>
        </el-table-column>
        <el-table-column align="center" label="" min-width="160">
          <template slot-scope="scope">
            <el-tooltip effect="light" :content="scope.row.title" placement="top">
              <el-link type="primary" :underline="false" @click="editDialogRow(scope.row)"
                >{{ scope.row.title }}
              </el-link>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column align="center" label="" width="160">
          <template slot-scope="scope">
            {{ scope.row.contactPerson }}
          </template>
        </el-table-column>
        <el-table-column align="center" label="" width="120">
          <template slot-scope="scope">
            <span>{{ scope.row.phone }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="" width="120">
          <template slot-scope="scope">
            <span>{{ scope.row.area }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="" width="160">
          <template slot-scope="scope">
            <span>{{ scope.row.pubTime }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="" min-width="160">
          <template slot-scope="scope">
            <el-popover placement="top-start" title="" width="250" trigger="hover">
              <div>{{ scope.row.description }}</div>
              <span
                slot="reference"
                v-if="scope.row.hasOwnProperty('description') && JSON.stringify(scope.row.description).length > 15"
                >{{ scope.row.description.substr(0, 15) }}...
              </span>
              <span v-else slot="reference">
                {{ scope.row.description }}
              </span>
            </el-popover>
          </template>
        </el-table-column>

        <el-table-column align="center" label="" width="120">
          <template slot-scope="scope">
            <el-button
              v-if="scope.row.status === '0'"
              type="danger"
              size="mini"
              style="width: 80px"
              @click="handleModifyStatus(scope.row, '1')"
            >
              
            </el-button>
            <el-button
              v-if="scope.row.status === '1'"
              type="success"
              size="mini"
              style="width: 80px"
              @click="handleModifyStatus(scope.row, '-1')"
            >
              
            </el-button>
            <el-button
              v-if="scope.row.status === '-1'"
              type="warning"
              size="mini"
              style="width: 90px"
              @click="handleModifyStatus(scope.row, '0')"
            >
              
            </el-button>
            <el-button
              v-if="scope.row.status !== '-1' && scope.row.status !== '0' && scope.row.status !== '1'"
              type="info"
              size="mini"
              style="width: 90px"
              @click="handleModifyStatus(scope.row, '0')"
            >
              
            </el-button>
          </template>
        </el-table-column>
        <el-table-column align="center" label="" width="200">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="editDialogRow(scope.row)"
              >
            </el-button>
            <el-button size="mini" type="danger" icon="el-icon-delete" @click="deleteRow(scope.row.id)"
              >
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="listQuery.pageNum"
        :page-sizes="[15, 20, 25, 30]"
        :page-size="listQuery.pageRow"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      v-cloak
      title=""
      @close="DialogClose()"
      center
      :visible.sync="dialogShow"
      top="7vh"
      width="50%"
      class="row-form"
    >
      <el-form :model="row" label-width="100px" class="demo-ruleForm" :rules="rules" ref="addFormRef">
        <el-tabs type="border-card">
          <el-tab-pane label="">
            <el-row>
              <el-col :span="12">
                <el-form-item label=":" prop="title">
                  <el-input v-model="row.title" clearable maxlength="20" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="" prop="pubType">
                  <el-select v-model="row.pubType" clearable style="width: 100%" placeholder="">
                    <el-option
                      v-for="item in pubTypeOptions"
                      :key="item.display_name"
                      :label="item.display_name"
                      :value="item.key"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                <el-form-item label=":" prop="contactPerson">
                  <el-input v-model="row.contactPerson" clearable maxlength="20" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop="phone">
                  <el-input v-model="row.phone" clearable maxlength="11" placeholder=""></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="12">
                <el-form-item label=":" prop="area">
                  <el-input v-model="row.area" clearable maxlength="20" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop="status">
                  <el-radio v-model="row.status" label="0"></el-radio>
                  <el-radio v-model="row.status" label="1"></el-radio>
                  <el-radio v-model="row.status" label="-1"></el-radio>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="12">
                <el-form-item label=":" prop="description">
                  <el-input
                    v-model="row.description"
                    clearable
                    type="textarea"
                    :autosize="{ minRows: 5, maxRows: 10 }"
                    placeholder=""
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label=":" prop="pubTime">
                  <el-date-picker v-model="row.pubTime" type="datetime" style="width: 100%" placeholder="">
                  </el-date-picker>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="24" class="register-upload">
                <el-form-item label=":" prop="pic">
                  <el-upload
                    :action="updateUrl"
                    :http-request="httpRequest"
                    :before-upload="beforeAvatarUpload"
                    :on-preview="handleProductPreview"
                    :on-remove="handleProductRemove"
                    :on-change="handleProductChange"
                    list-type="picture-card"
                    :file-list="fileProductList"
                  >
                    <i class="el-icon-plus"></i>
                  </el-upload>
                  <!--  -->
                  <el-dialog :visible.sync="dialogProductVisible" width="70%" top="5vh" :append-to-body="true">
                    <img width="100%" :src="imageUrl" alt="" />
                  </el-dialog>

                  <div class="upload-div">
                    <div class="upload-div-list" v-for="(item, index) of imgRemoveLists" :key="index">
                      <div class="upload-div-top">
                        <span class="upload-div-del" @click="fileDelIdPhoto(index, item.id)">X</span>
                      </div>
                      <el-image
                        class="el-image-preview"
                        :src="'http://' + item.path"
                        :preview-src-list="srcList"
                      ></el-image>
                    </div>
                  </div>
                </el-form-item>
              </el-col>
            </el-row>
          </el-tab-pane>
        </el-tabs>
        <span class="t-btn">
          <el-button type="info" @click="dialogShow = false"> </el-button>
          <el-button type="success" @click="submitRow"> </el-button>
        </span>
      </el-form>
    </el-dialog>
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" :src="dialogImageUrl" alt="" />
    </el-dialog>
  </div>
</template>

<script>
import userService from '../../globals/service/user';

const stateTypeOptions = [
  { key: '0', display_name: '' },
  { key: '1', display_name: '' },
  { key: '-1', display_name: '' }
];
const pubTypeOptions = [
  { key: '0', display_name: '' },
  { key: '1', display_name: '' }
];

export default {
  data() {
    return {
      // 
      stateTypeOptions,
      // 
      pubTypeOptions,
      activeName: 'all',
      // 
      listQuery: {
        page: 1,
        size: 15,
        pubType: '', //
        title: '', //
        contactPerson: '' // 
      },
      total: 0,
      // 
      rows: [],
      // 
      row: {
        title: '', //
        contactPerson: '', //
        phone: '', // 
        area: '', // 
        description: '', // 
        pubType: '', // 
        pubTime: '', // 
        status: '', // 
        imgLists: [] //
      },
      //id
      deleteIds: [],
      // src
      srcList: [],
      dialogProductVisible: false,
      productRemoveListPhoto: [],
      //
      imgRemoveLists: [],
      //
      fileProductList: [],
      // 
      updateUrl: '#',
      imageUrl: '',
      // 
      dialogShow: false,
      listLoading: false,
      // 
      curHeight: 0,
      //ids
      ids: [],
      //
      flag: 'add',
      // 
      rules: {
        title: [{ required: true, message: '', trigger: 'blur' }],
        contactPerson: [{ required: true, message: '', trigger: 'blur' }],
        phone: [{ required: true, message: '', trigger: 'blur' }],
        area: [{ required: true, message: '', trigger: 'blur' }],
        description: [{ required: true, message: '', trigger: 'blur' }],
        pubType: [{ required: true, message: '', trigger: 'blur' }],
        status: [{ required: true, message: '', trigger: 'blur' }]
      },
      dialogImageUrl: '',
      dialogVisible: false
    };
  },
  created() {
    this.getList();
    this.setTableHeight();
    window.onresize = () => {
      this.setTableHeight();
    };
  },
  methods: {
    //
    fileDelIdPhoto(index, id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.deleteIds.push(id);
          this.imgRemoveLists.splice(index, 1);
        })
        .catch(() => {
          this.$message({
            duration: 1000,
            type: 'info',
            message: ''
          });
        });
    },
    //
    handleProductChange(file, fileList) {
      this.fileProductList = fileList;
    },
    //
    handleProductRemove(file, fileLists) {
      for (let i in this.productRemoveListPhoto) {
        if (this.productRemoveListPhoto[i].key === file.uid) {
          this.productRemoveListPhoto.splice(i, 1);
        }
      }
      this.fileProductList = fileLists;
    },
    handleProductPreview(file) {
      this.imageUrl = file.url;
      this.dialogProductVisible = true;
    },
    // 
    getList() {
      this.listLoading = true;
      if (this.activeName === 'supply') {
        this.listQuery.pubType = '0';
      } else if (this.activeName === 'demand') {
        this.listQuery.pubType = '1';
      } else if (this.activeName === 'all') {
        this.listQuery.pubType = '';
      }
      let params = this.listQuery;
      userService.getSupplyDemandListByPage(params).then(res => {
        if (res.status !== 200) return this.$message.error('');
        this.rows = res.data.rows;
        this.listLoading = false;
        this.total = res.data.total;
      });
    },
    //url
    handleImgUrl(data) {
      let imgList = [];
      data.map(item => {
        imgList.push('http://' + item);
      });
      return imgList;
    },
    // 
    handleFilter() {
      this.listQuery.page = 1;
      this.getList();
    },
    //tab
    handleClick() {
      console.log('tab');
      this.getList();
    },
    //
    checkSelect(data) {
      console.log(data);
      data.forEach(item => {
        this.ids.push(item.id);
      });
    },
    async handleBatchRemove() {
      if (this.ids.length === 0) return this.$message.warning('');
      const confirmResult = await this.$confirm(',?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err);
      if (confirmResult !== 'confirm') return this.$message.info('');
      userService.delSupplyDemandById(this.ids).then(res => {
        if (res.status !== 200) return this.$message.error('');
        this.$message.success('');
        this.getList();
      });
    },
    // 
    handleModifyStatus(row, item) {
      let status;
      if (item === '-1') status = '';
      if (item === '0') status = '';
      if (item === '1') status = '';
      this.$confirm(` ${status} ?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          // console.log(row, status);
          row.status = item;
          userService.updateSupplyDemandStatus(row).then(res => {
            if (res.status === 200) {
              this.$message({
                message: `${status}`,
                type: 'success'
              });
            }
          });
        })
        .catch(() => {
          this.$message({ type: 'info', message: '' });
        });
    },
    // 
    addRow() {
      this.flag = 'add';
      if (this.activeName === 'supply') {
        this.row.pubType = '0'; // 
      } else if (this.activeName === 'demand') {
        this.row.pubType = '1'; // 
      } else {
        this.row.pubType = '0'; // 
      }
      this.row.pubTime = this.$moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
      (this.row.status = '1'), (this.dialogShow = true);
    },
    // 
    setTableHeight() {
      let h = document.documentElement.clientHeight || document.body.clientHeight;
      this.curHeight = h - 280;
    },
    // 
    editDialogRow(row) {
      this.flag = 'edit';
      this.row = row;
      this.imgRemoveLists = row.imgLists;
      row.imgLists.map(item => {
        this.srcList.push('http://' + item.path);
      });
      this.dialogShow = true;
    },
    //
    handleSizeChange(newSize) {
      this.listQuery.size = newSize;
      this.getList();
    },
    //
    handleCurrentChange(newPage) {
      this.listQuery.page = newPage;
      this.getList();
    },
    // 
    async deleteRow(id) {
      let ids = [];
      ids.push(id);
      const confirmResult = await this.$confirm(',?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err);
      if (confirmResult !== 'confirm') return this.$message.info('');
      userService.delSupplyDemandById(ids).then(res => {
        if (res.status !== 200) return this.$message.error('');
        this.$message.success('');
        this.getList();
      });
    },
    DialogClose() {
      this.fileProductList = [];
      this.imgRemoveLists = [];
      this.deleteIds = [];
      this.$refs.addFormRef.resetFields();
      this.$refs.addFormRef.clearValidate();
      this.getList();
    },
    // 
    httpRequest(data) {
      let rd = new FileReader(); // 
      let file = data.file;
      rd.readAsDataURL(file); // base64
      rd.onloadend = function() {
        this.imageUrl = this.result; // thisonloadend
      };
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isPng = file.type === 'image/png';
      if (!isJPG && !isPng) {
        this.$message.error('.jpg.png!');
      }
      // const isLt10M = file.size / 1024 / 1024 < 10;
      // if (!isLt10M) {
      //     this.$message.error(' 10MB!');
      // }
      return isJPG || isPng;
    },
    handleExceed() {
      this.$message.warning(` ${this.limit} `);
    },
    //  
    submitRow() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return this.$message.error('');
        let formData = new FormData();
        formData.append('files', this.multfileImg);
        formData.append('title', this.row.title);
        formData.append('contactPerson', this.row.contactPerson);
        formData.append('phone', this.row.phone);
        formData.append('status', this.row.status);
        this.row.pubTime = this.$moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
        formData.append('pubTime', this.row.pubTime);
        formData.append('area', this.row.area);
        formData.append('pubType', this.row.pubType);
        formData.append('description', this.row.description);
        this.fileProductList.forEach(item => {
          formData.append('files', item.raw);
        });
        if (this.flag === 'add') {
          console.log('');
          console.log(':', this.row.status);
          console.log(':', this.row.pubType);
          userService.addSupplyDemand(formData).then(res => {
            if (res.status !== 200) return this.$message.error('');
            this.$message.success('');
            this.dialogShow = false;
            this.getList();
          });
        } else if (this.flag === 'edit') {
          console.log('');
          this.deleteIds.map(item => {
            console.log(item);
            formData.append('delIds', item);
          });
          formData.append('id', this.row.id);
          userService.updateSupplyDemand(formData).then(res => {
            if (res.status !== 200) return this.$message.error('');
            this.$message.success('');
            this.dialogShow = false;
            this.getList();
          });
        }
      });
    }
  }
};
</script>

<style lang="less" scoped></style>
<style lang="less">
.register-upload {
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    width: 220px;
    height: 220px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409eff;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 220px;
    height: 220px;
    line-height: 220px;
    text-align: center;
  }

  .avatar {
    width: 220px;
    height: 220px;
    display: block;
  }
}

.upload-div {
  width: 350px;
  display: flex;
  justify-content: start;
  align-items: center;
  flex-wrap: wrap;
}

.upload-div-del {
  cursor: pointer;
}

.upload-div-del:hover {
  color: #64b7ff;
}

.link-type,
.link-type:focus {
  color: #337ab7;
  cursor: pointer;

  & :hover {
    color: rgb(32, 160, 255);
  }
}

.products-img-container {
  height: 100px;
  padding: 10px;
  overflow: auto;
}

.el-image-preview {
  width: 100px;
  height: 100px;
  margin-right: 10px;
}
</style>
